@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
.header{width:100%;display:flex;flex-direction:row;justify-content:flex-end;align-items:center;padding-right:40px}.header__btn{display:flex;align-items:center;font-size:20px;font-weight:bold;background-color:transparent;text-transform:uppercase;padding:10px;cursor:pointer}

.notification{position:absolute;bottom:20px;right:20px;z-index:99;min-width:20%;border:1px solid #ffeeba !important;border-radius:5px;color:#856404;background-color:#fff3cd;padding:20px;font-size:20px;font-weight:bold;transform-origin:right;animation:notify 3s ease-in-out infinite}@keyframes notify{0%{transform:scaleX(0);opacity:0}10%{transform:scaleX(1);opacity:1}16%{transform:scale(1)}55%{transform:scaleX(1);opacity:1}65%{transform:scaleX(0.25);opacity:0}95%{transform:scaleX(0.25)}100%{transform:scaleX(0)}}

.nav{font-size:16px;padding:36px 32px;background:var(--card-bg);-webkit-backdrop-filter:blur(20px);backdrop-filter:blur(20px);border-radius:16px}.nav__logo{width:100%;padding-bottom:36px}.nav__link{display:flex;align-items:center;width:100%;padding:12px 27px;text-decoration:none}.nav__link--active{background:var(--nav-active-bg);box-shadow:0px 0px 1px rgba(40,41,61,0.04),0px 2px 4px rgba(96,97,112,0.16);border-radius:8px;color:var(--nav-active-text)}.nav__link svg{margin-right:22px}

.container{display:flex;width:100vw;height:100vh;background:var(--bg);padding:32px}.container__right{width:100%}.container__right .main{position:relative;height:100%;padding:52px;padding-top:10px}.bg-green-circle-1{z-index:0;position:absolute;width:472px;height:472px;left:-8px;top:560px;border-radius:100%;background:#91f466;opacity:0.5}.bg-green-circle-2{z-index:0;position:absolute;width:720px;height:720px;top:312px;right:-472px;border-radius:100%;background:#91f466;opacity:0.9}

.app{display:block;width:280px;height:456px;border-radius:16px;background:var(--card-bg);-webkit-backdrop-filter:blur(20px);backdrop-filter:blur(20px);padding:16px;padding-bottom:0}.app__header{display:flex;margin-bottom:24px !important}.app__view-in-store{display:flex;align-items:center;justify-content:space-between;margin-bottom:16px}.app__view-in-store div{margin-bottom:0}.app__icon{width:88px;margin-right:16px;-webkit-filter:drop-shadow(0px 2px 4px rgba(40,41,61,0.04)),drop-shadow(0px 8px 16px rgba(96,97,112,0.16));filter:drop-shadow(0px 2px 4px rgba(40,41,61,0.04)),drop-shadow(0px 8px 16px rgba(96,97,112,0.16));border-radius:8px}.app__screenshots{overflow:hidden;display:flex;margin-bottom:24px}.app__screenshot{margin-right:8px}.app__rent-btn{font-size:12px;width:100%;padding:12px;text-align:center;background:var(--btn-bg);color:var(--btn-text);font-weight:600;box-shadow:0px 2px 4px rgba(40,41,61,0.04),0px 8px 16px rgba(96,97,112,0.16);border-radius:8px;cursor:pointer}.app__cabinets-over{font-size:12px;width:100%;padding:12px;text-align:center;font-weight:600;background:var(--inactive-btn-bg);color:var(--inactive-btn-text);border-radius:8px}.app__in-rent{font-size:12px;display:block;width:100%;padding:2px;text-align:center;font-weight:500;color:var(--inactive-btn-text)}.app__in-rent *{display:block}

.apps{width:100%;overflow-x:hidden;padding-right:24px}.apps__price{padding:0 10px}.apps__filter{padding:11px;border-radius:16px;background:var(--card-bg);-webkit-backdrop-filter:blur(20px);backdrop-filter:blur(20px);margin-bottom:24px;display:flex;flex-wrap:wrap}.apps__filter *{color:#1d1d1d}.apps__filter .range{display:flex;justify-content:space-between}.apps__filter .range div{color:var(--text)}.apps__filter .input{margin:5px;display:flex;align-items:center;box-shadow:inset 0px 0.5px 4px rgba(96,97,112,0.32);background:#ebebea;border-radius:8px;height:40px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.apps__filter .input--slim{height:auto;padding:3px}.apps__filter .input svg{margin:11px 19px}.apps__filter .input input{background:transparent;font-weight:bold;font-size:14px}.apps__filter .input__slider{width:200px;margin:0 5px;background-color:var(--btn-bg)}.apps__filter .input__slider .rc-slider-rail{background:transparent !important}.apps__filter .input__slider .rc-slider-track{background:transparent !important}.apps__filter .input__slider .rc-slider-handle{background:#000 !important;border-color:var(--btn-bg)}.apps__filter .input__btns-group{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.apps__filter .input__btns-group .btn{font-weight:bold;font-size:14px;padding:7px 25px;margin:4px 5px;border-radius:4px;background:transparent;cursor:pointer}.apps__filter .input__btns-group .btn--active{background:var(--btn-bg)}.apps__grid{overflow-y:auto;height:calc(100vh - 64px - 10px - 44px - 72px - 24px);width:100%;display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-gap:24px}@media (max-width: 1600px){.apps__grid{grid-template-columns:1fr 1fr 1fr}}@media (max-width: 1300px){.apps__grid{grid-template-columns:1fr 1fr}}@media (max-width: 1100px){.apps__grid{grid-template-columns:1fr}}

.offers{width:100%;display:flex;flex-wrap:wrap;position:relative;height:100%;overflow:auto}.offers__left,.offers__right{width:50%;min-width:480px;position:relative;padding:0 12px}.offers__left>div,.offers__right>div{margin-bottom:24px}@media (max-width: 1420px){.offers__left,.offers__right{width:100%}}.offers__ads{width:100%;border-radius:16px;padding:16px;background:var(--card-bg);-webkit-backdrop-filter:blur(20px);backdrop-filter:blur(20px)}.offers__ads img{width:100%;border-radius:8px}.offers .form{border-radius:16px;padding:32px;background:var(--card-bg);-webkit-backdrop-filter:blur(20px);backdrop-filter:blur(20px);position:relative}.offers .form__row{width:100%;display:flex;flex-wrap:wrap;position:relative}.offers .form__row--msg{justify-content:space-between;margin-top:29px}.offers .form__msg{height:40px;display:flex;flex:1 1;width:100%;align-items:center}.offers .form__msg svg{margin-right:10px}.offers .form__submit{font-size:14px;padding:12px 32px;margin:5px;text-align:center;background:var(--btn-bg);color:var(--btn-text);font-weight:600;box-shadow:0px 2px 4px rgba(40,41,61,0.04),0px 8px 16px rgba(96,97,112,0.16);border-radius:8px;cursor:pointer}.offers .form__input-holder{flex:1 1}.offers .form__input-title{padding-left:15px}.offers .form select{width:100%;padding:10px;margin:5px;background:var(--select-bg);color:var(--select-text);height:40px;border-radius:8px;font-weight:bold}.offers .form select option{color:var(--select-text)}.offers .form__input{margin:5px;display:flex;align-items:center;justify-content:space-between;padding:0 16px;box-shadow:inset 0px 0.5px 4px rgba(96,97,112,0.32);background:var(--input-bg);border-radius:8px;height:40px}.offers .form__input svg{margin:11px 19px;margin-right:0}.offers .form__input input{background:transparent;font-weight:bold;font-size:14px;min-width:100px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.offers .form__input input::-webkit-input-placeholder{overflow:hidden;text-overflow:ellipsis}.offers .form__input input:-ms-input-placeholder{overflow:hidden;text-overflow:ellipsis}.offers .form__input input::-ms-input-placeholder{overflow:hidden;text-overflow:ellipsis}.offers .form__input input::placeholder{overflow:hidden;text-overflow:ellipsis}.offers .table{border-radius:16px;padding:32px;background:var(--card-bg);-webkit-backdrop-filter:blur(20px);backdrop-filter:blur(20px);overflow-y:auto}.offers .table__row{display:flex;justify-content:space-between;border-bottom:1px solid #ccc}.offers .table__row div{width:23%;overflow:hidden;white-space:nowrap;text-overflow:ellipsis;padding:8px 0}.offers .table__row div:last-of-type{width:8%;display:flex;align-items:center;justify-content:center;cursor:pointer}.offers .table__row--header{border-bottom:none}

*{margin:0;padding:0;border:none;outline:none;box-sizing:border-box}body{--bg: #ebebea;--card-bg: rgba(245, 245, 244, 0.7);--text: #333333;--nav-active-bg: #333333;--nav-active-text: #f5f5f5;--btn-bg: #55ef14;--btn-text: #333333;--inactive-btn-text: #707070;--inactive-btn-bg: #ebebea;--select-bg: #525252;--select-text: #f5f5f4;--input-bg: #ebebea}body.theme-dark{--bg: #1d1d1d;--card-bg: rgba(51, 51, 51, 0.65);--text: #ccc;--nav-active-bg: #55ef14;--nav-active-text: #525252;--inactive-btn-text: #999999;--inactive-btn-bg: #333333;--select-bg: #707070;--input-bg: #ccc}*{font-family:"Inter", sans-serif;color:var(--text)}body{overflow:hidden;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

